import { Component, Vue } from 'vue-property-decorator'
import { getRentEducation } from '@/api/report/reportApi'
import { getProjectList } from '@/api/manage/sys/houseManageApi'

@Component({
  components: {},
})
export default class degree extends Vue {
  private pageTitle = '租客学历分析'
  private query = {
    projectId: '',
    endDate: '',
    startDate: '',
  }
  // 项目列表
  private projectList: any[] = []
  // x轴的数组
  private dataList: any[] = []
  // y轴的数组
  private dataValueList: any[] = []
  public mounted(): void {
    this.requestProjectList()
  }
  /**
   * @Author roct
   * @Description 获取项目列表
   * @Date 10:51 2021/5/30 0030
   **/
  private async requestProjectList() {
    const project = await getProjectList()
    this.projectList = project.data
    await this.getEducation()
  }
  /**
   * @Author roct
   * @Description 获取学历分析
   * @Date 10:51 2021/5/30 0030
   **/
  private async getEducation() {
    const res = await getRentEducation(this.query)
    this.dataList = []
    this.dataValueList = []
    for (const item of res.data) {
      this.dataList.push(item.name)
      this.dataValueList.push(item.value)
    }
    this.echartsInit()
  }
  /**
   * @Author roct
   * @Description initEcharts
   * @Date 10:51 2021/5/30 0030
   **/
  private echartsInit() {
    //柱形图
    // @ts-ignore
    this.$echarts.init(document.getElementById('education')).setOption({
      tooltip: {
        trigger: 'axis',
        // axisPointer: {            // 坐标轴指示器，坐标轴触发有效
        //   type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
        // }
      },
      xAxis: {
        type: 'category',
        data: this.dataList,
        axisTick: {
          alignWithLabel: true,
        },
      },
      yAxis: {
        type: 'value',
      },
      series: [
        {
          data: this.dataValueList,
          type: 'bar',
          showBackground: true,
          barWidth: '20px',
        },
      ],
    })
  }
}
